.LoginPageBody {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100vw;
	height: 100vh;

	z-index: 200;
	position: absolute;
	top: 0;
	left: 0;
}
.Form {
	border: solid rgba(128, 128, 128, 0.547) 3px;
	padding: 20px 20px;
	border-radius: 20px;
	background-color: white;
	display: flex;
	flex-direction: column;
	width: 50vw;
	color: black;
	position: absolute;
	@media (max-width: 1200px) {
		width: 70vw;
		height: 90vh;
	}
}
.Overlay {
	background-color: rgba(198, 198, 198, 0.821);
	width: 100vw;
	height: 100vh;
	z-index: -1;
	position: absolute;
	top: 0;
	left: 0;
}
.LoginPageTitle {
	font-size: 40px;
	font-weight: 700;
	margin: 0px 0px 1vw 0px;
}
.RedirectOnSignIn {
	text-decoration: underline;
	font-weight: 400;
	cursor: pointer;
}
.InputContainer {
	font-size: 25px;
	margin: 0px 0px 1vw 0px;
	& input {
		padding: 5px 10px;
		width: 100%;
		background-color: rgba(128, 128, 128, 0.11);
		border-radius: 10px;
	}
}
.Error {
	color: red;
}
.LableContainer {
	display: flex;
	& :not(:last-child) {
		margin: 0px 20px 0px 0px;
	}
}
.FormButton {
	display: flex;
	justify-content: center;
	align-items: center;
	align-self: center;
	border-radius: 5px;
	width: 200px;
	height: 40px;
	background-color: rgb(228, 157, 32);
	font-size: 25px;
	&:hover {
		background-color: rgb(189, 125, 15);
	}
}
