.TopBarCart {
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 20px;
	margin: 0px 0px 0px 4vw;
	cursor: pointer;
	height: 100%;

	@media (max-width: 1000px){
		flex: 0 1 50%;
		margin: 0px 0px 0px 0vw;
	}

}
.Succsess {
	font-size: 30px;
	background-color: rgba(100, 159, 17, 0.858);
	color: white;
	position: absolute;
	top: 0;
	left: 50%;
	padding: 10px 30px;
	border-radius: 0px 0px 10px 10px;
	transform: translate(-50%);
	animation-name: move-succsess;
	animation-duration: 4s;
	animation-fill-mode: forwards;
	z-index: 2000;
}
@keyframes move-succsess {
	0%{
		top: -50px;
	}
	50%{
		top: 0px;
	}
	100%{
		top: -50px;
	}
}
.TopBarCartItems_container {
	display: flex;
	justify-content: flex-end;
	position: absolute;
	height: 100vh;
	width: 100vw;
	top: 0;
	left: 0;
	cursor: default;
}
.Overlay {
	position: absolute;
	z-index: 50;
	height: 100vh;
	width: 100vw;
	background-color: rgba(96, 96, 96, 0.608);
	top: 0;
	left: 0;
	cursor: default;
}
.TopBarCartItemsBody {
	background-color: #fff;
	z-index: 100;
	width: 25vw;
	padding: 15px 10px;
	position: relative;
	animation-duration: 0.5s;
	animation-name: show-cart;
	@media (max-width: 1200px){
		width: 50vw;
	}
	@media (max-width: 600px){
		width: 100vw;
	}
}
@keyframes show-cart {
	0%{
		left: 100%;
	}
	100%{
		left: 0;
	}
}

.ItemsBodyButton {
	width: 40px;
	height: 40px;
	position: relative;
	&::before {
		content: "";
		left: 0;
		top: 50%;
		position: absolute;
		width: 100%;
		height: 2px;
		background-color:	rgb(0, 0, 0);
		transform: rotate(45deg);
	}
	&::after {
		content: "";
		left: 0;
		top: 50%;
		position: absolute;
		width: 100%;
		height: 2px;
		background-color: black;
		transform: rotate(-45deg);
	}
}

