.CartItems {
	height: calc(100vh - 75px);
	overflow: auto;
	margin: 20px 0px 0px 0px;
	display: flex;
	flex-direction: column;

}


.CartTitle {
	font-size: 2.2rem;
	color: black;
	margin: 0px 0px 2rem 0px;
	align-self: center;
}

.CartList {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	margin: 0px 0px 2rem 0px;
	justify-self: flex-end;
	&:last-child {
		margin: 0px 0px 50px 0px;
	}
}
.TotalSumm {
	bottom: 0;
	left: 50%;
	color: black;
	font-size: 2.2rem;
	margin: 0px 0px 2rem 0px;
}
.ConfirmBtn {
	text-transform: uppercase;
	border-radius: 5px;
	width: 100%;
	height: 40px;
	background-color: rgb(228, 157, 32);
	@media (max-width: 1200px){
		border-radius: 0px;
		height: 60px;
	}
	@media (hover: hover) {
    &:hover{background-color: rgb(189, 125, 15);}
}

}

.CartBottomBar {
	background-color: #fff;
	position: absolute;
	width: 100%;
	bottom: 0;
	left: 0;
	flex-direction: column;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 20px 10px;
	@media (max-width: 1200px){
		padding: 20px 0px 0px 0px;
	}
}